<template>
    <div ref="wrapper">
        <slot></slot>
    </div>    
</template>

<script>
import BScroll from 'better-scroll'
// import { setClientHeight } from 'common/js/dom'

export default {
    props: {
        /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
        probeType: {        //scroll
            type: Number,
            default: 1
        },
        click: {            //
            type: Boolean,
            default: true
        },
        listenScroll: {     //
            type: Boolean,
            default: false
        },
        data: {
            type: Array,
            default: null
        },
        pullup: {
            type: Boolean,
            default: false
        },
        pulldown: {
            type: Boolean,
            default: false
        },
    },
    methods: {
        initScroll() {
            if(!this.$refs.wrapper) { return }
            this.scroll = new BScroll(this.$refs.wrapper, {
                probeType: this.probeType,
                click: this.click,
                pullUpLoad: {           // 
                    threshold: -80      //80px
                },
                mouseWheel: {           // pc
                    speed: 20,
                    invert: false
                },
                useTransition: false,  // iphone
            });

            // this.scroll.on('pullingUp', ()=>{           //
            //     this.scrollFinish = false;
            //     // ,ajaxfinish,
            //     this.scroll.finishPullUp();
            //     // 
            //     this.getIncomeDetail(this.nextPage);
            // });

            // 
            if (this.pullup) {
                this.scroll.on('scrollEnd', () => {
                        // 
                    if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
                        this.$emit('scrollToEnd')
                    }
                })
            }

            // 
            if (this.pulldown) {
                this.scroll.on('touchend', (pos) => {
                        // 
                    if (pos.y > 50) {
                        this.$emit('pulldown')
                    }
                })
            }
        },
        enable() {
            this.scroll && this.scroll.enable()
        },
        disable() {
            this.scroll && this.scroll.disable()
        },
        refresh() {
            this.scroll && this.scroll.refresh()
        }
    },
    mounted() {
        // wrapper
        // this.$refs.wrapper.style.height = setClientHeight() - this.$refs.wrapper.offsetTop + "px";
        // better-scroll contentwrapper,
        //   ,wrapper,n
        // contentmin-heightwrapper
        // this.$refs.wrapper.children[0].style.minHeight = this.$refs.wrapper.offsetHeight + 1 + "px";

        //   this._initScroll();
        //   this.getIncomeDetail(this.nextPage);

        // scroll
        // this.scrollHeight = document.getElementById("app").offsetHeight - document.getElementById("scroll").offsetTop ;


        this.$nextTick(() => {
            this.initScroll()
        })
    },
    watch: {
        data() {
            setTimeout(() => {      //BScroll
                this.refresh()
            }, 20);
        }
    }
}
</script>

<style lang="scss" scoped>

</style>



