<template>
    <div class="fiction-info">
        <div class="top">
            <div>
                <img src="../../common/images/fiction/fiction.jpg" />
            </div>
            <div>
                <div>
                    <p><span>{{''}}</span>  {{''}}</p>
                    <p><span>{{'2018-05-05 15:30'}}</span></p>
                </div>
                <div>
                    
                    
                    
                </div>
                <p>{{'11'}}</p>
            </div>
        </div>
        <div class="record">
            <span><i class="self-icon-eye"></i></span>{{132151}}
            <span><i class="self-icon-star-full"></i></span>{{123333}}
        </div>
        <div class="fiction-list">
            <h2></h2>
            <ul>
                <li>1233</li>
                <li>1</li>
                <li>2</li>
                <li>3</li>
                <li>4</li>
            </ul>
            <p class="more"> >></p>
        </div>
        <div class="comment">
            <h2></h2>
            <ul>
                <li v-for="(item, index) in data" :key="index">
                    <div><img src="../../common/images/fiction/fiction.jpg" alt=""></div>
                    <div>
                        <p>  <span>{{'2018-05-10'}}</span></p>
                        <div>
                                 
                                 
                             
                        </div>
                    </div>
                </li>
            </ul>
        </div>

        <!--  -->
        <div class="comment-input">
            <textarea ref="textarea" v-model="commentInput" placeholder="~" style="height: auto" @input='setHeight'></textarea> <div></div>
        </div>
    </div>
</template>

<script>
import { mapMutations, mapActions } from 'vuex'
import { autoTextAreaHeight } from 'common/js/dom'

export default {
    data() {
        return {
            titleInfo: {
                title: '',
                showIcon: false
            },
            commentInput: '',
            data: [
                {}
            ],
        }
    },
    methods: {
        ...mapActions([ 'handleTitle']),
        handleThumbs() {
            console.log('')
            this.isThumbs = !this.isThumbs
            this.thumbsNum = this.isThumbs ? this.thumbsNum+1 : this.thumbsNum-1
        },
        setHeight() {
            autoTextAreaHeight(this.$refs.textarea)
        } 
    },
    mounted() {
        this.handleTitle({
            title:    this.titleInfo.title, 
            showIcon: this.titleInfo.showIcon,
        })

    }
}
</script>

<style lang="scss" scoped>
@import "../../common/css/index.scss";

.fiction-info {
    padding: 15px;
    @include box-sizing;
    font-size: $font-size-small;  /*no*/
    .top {
        padding: 25px 35px;
        display: flex;
        @include color-background;
        >div {
            &:first-child {
                width: 170px;
                min-width: 170px;
                text-align: center;
                img {
                    width: 140px;
                    height: 200px;
                }
            }
            &:last-child {
                width: 100%;
                height: 200px;
                @include flex-colum;
                justify-content: space-between;
                text-align: justify;
                >p { font-size: $font-size-min;  /*no*/ color: $text-color-l; }
                >div {
                    &:first-child {
                        color: $text-color-dark;
                        p {
                            font-size: $font-size-min;  /*no*/
                            &:first-child { 
                                margin-bottom: 10px;
                                span {
                                    font-size: $font-size-normal; /*no*/
                                    color: black;
                                    font-weight: 600;
                                    margin-right: 30px;
                                }
                             }
                        }
                    }
                    &:nth-child(2) {
                        line-height: 1.2em;
                        height: 3.4em;
                        max-height: 3.4em;
                        @include lines-nowrap(3);
                    }
                }
            }
        }      
    }
    .record {
        margin: 15px 0;
        height: 60px;
        line-height: 60px;
        padding: 0 50px;
        @include color-background;
        font-size: $font-size-min;  /*no*/
        span { 
            color: $text-color-orange; 
            font-size: $font-size-small;  /*no*/
            i { margin-right: 10px; }
            &:nth-child(2) { margin-left:30px }
        }
    }
    .fiction-list {
        padding: 20px 60px;
        @include color-background;
        line-height: 1.5em;
        h2 { margin-bottom: 15px; } 
        .more { text-align: center; color:$text-color-orange; font-size: $font-size-min; /*no*/ }
    }
    .comment {
        margin-top: 15px;
        padding-top: 15px;
        @include color-background;
        >h2 { margin-bottom: 15px; padding-left: 60px; }
        >ul {
            li {
                display: flex;
                @include box-sizing;
                padding: 15px 60px;
                border-bottom: 5px solid #ebebeb;  /*no*/
                div {
                    &:first-child {
                        width: 45px;
                        min-width: 45px;
                        img { width: 35px; height: 35px; @include border-radius(50%); }
                        margin-right: 10px;
                    }
                    &:nth-child(2) {
                        width: 100%;
                        >p { 
                            margin: 5px 0 15px;
                            span { float: right; font-size: $font-size-min;  /*no*/ color: $text-color-l; }
                         }
                        >div {
                            text-align: justify;
                            color: $text-color-dark;
                            line-height: 1.3em;
                        }
                    }
                }
            }
        }
    }
    .comment-input {
        padding: 35px 0 50px;
        @include box-sizing;
        display: flex;
        textarea {
            @include box-sizing;
            width: 100%;
            min-height: 3em;
            border: 1px solid $border-color-d;  /*no*/
            // line-height: 1.5em;
            padding: .8em 1em 0;
        }
        >div {
            margin-left: 10px;
            font-weight: 800;
            width: 25%;
            min-width: 6em;
            height: 3em;
            line-height: 3em;
            text-align: center;
            color: white;
            background: $text-color-orange;
        }
    }
}
</style>



